<Svg
  {label}
  {width}
  {height}
  {box}
  style={combinedStyle}
  {spin}
  {flip}
  {inverse}
  {pulse}
  class={className}
  {...$$restProps}
>
  <slot>
    {#each iconData?.paths || [] as path}
      <path {...path} />
    {/each}
    {#each iconData?.polygons || [] as polygon}
      <polygon {...polygon} />
    {/each}
    {#if iconData?.raw}
      <Raw bind:data={iconData} />
    {/if}
  </slot>
</Svg>

<script context="module"></script>

<script>import Raw from './svg/Raw.svelte';
import Svg from './svg/Svg.svelte';
let className = '';
export let data;
let iconData;
export let scale = 1;
export let spin = false;
export let inverse = false;
export let pulse = false;
export let flip = undefined;
export let label = '';
export let style = '';
export { className as class };
// internal
let childrenHeight = 0;
let childrenWidth = 0;
let outerScale = 1;
let width = 10;
let height = 10;
let combinedStyle;
let box;
function normaliseData(data) {
    let name;
    let iconData;
    if (!data) {
        return undefined;
    }
    else if ('definition' in data) {
        console.error("`import faIconName from '@fortawesome/package-name/faIconName` not supported - Please use `import { faIconName } from '@fortawesome/package-name/faIconName'` instead");
        return undefined;
    }
    else if ('iconName' in data && 'icon' in data) {
        name = data.iconName;
        // fontawesome v5/6 icon imported with:
        // import { iconName } from '@fortawesome/packagename/iconName';
        // import { iconName } from '@fortawesome/packagename';
        const [width, height, , , path] = data.icon;
        const paths = Array.isArray(path) ? path : [path];
        iconData = {
            width,
            height,
            paths: paths.map((path) => {
                return { d: path };
            })
        };
    }
    else {
        // inbuilt icons
        name = Object.keys(data)[0];
        iconData = data[name];
    }
    return iconData;
}
function normalisedScale() {
    let numScale = 1;
    if (typeof scale !== 'undefined') {
        numScale = Number(scale);
    }
    if (isNaN(numScale) || numScale <= 0) {
        console.warn('Invalid prop: prop "scale" should be a number over 0.');
        return outerScale;
    }
    return numScale * outerScale;
}
function calculateBox() {
    if (iconData) {
        return `0 0 ${iconData.width} ${iconData.height}`;
    }
    return `0 0 ${width} ${height}`;
}
function calculateRatio() {
    if (!iconData) {
        return 1;
    }
    return Math.max(iconData.width, iconData.height) / 16;
}
function calculateWidth() {
    if (childrenWidth) {
        return childrenWidth;
    }
    if (iconData) {
        return (iconData.width / calculateRatio()) * normalisedScale();
    }
    return 0;
}
function calculateHeight() {
    if (childrenHeight) {
        return childrenHeight;
    }
    if (iconData) {
        return (iconData.height / calculateRatio()) * normalisedScale();
    }
    return 0;
}
function calculateStyle() {
    let combined = '';
    if (style !== null) {
        combined += style;
    }
    let size = normalisedScale();
    if (size === 1) {
        if (combined.length === 0) {
            return '';
        }
        return combined;
    }
    if (combined !== '' && !combined.endsWith(';')) {
        combined += '; ';
    }
    return `${combined}font-size: ${size}em`;
}
$: {
    iconData = normaliseData(data);
    style;
    scale;
    width = calculateWidth();
    height = calculateHeight();
    combinedStyle = calculateStyle();
    box = calculateBox();
}
</script>

<svelte:options namespace="svg" />
